@import '../../styles/variables';

.background {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba($color: #000000, $alpha: 0.8);
    z-index: 1000;
    .closeButton {
        cursor: pointer;
        text-align: end;
        position: relative;
        top: -45px;
        left: 40px;
    }
    .modal {
        text-align: center;
        background-color: rgb(51, 50, 50);
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 90%;
        border-radius: 10px;
        padding: 0 20px 50px 20px;
        .main {
            display: grid;
            grid:
            "title title"
            "video info";
            .title {
                grid-area: title;
                h1 {
                    font-size: 1.5em;
                    margin: 0;
                    padding-bottom: 10px;
                }
                h2 {
                    padding: 0;
                    margin: 0;
                    font-size: 1em;
                    padding-bottom: 15px;
                }
            }
            .video {
                grid-area: video;
                text-align: center;
            }
            .info{
                grid-area: info;
                .director {
                    h2 {
                        margin: 15px 0;
                        font-size: 1.2em;
                    }
                    h3 {
                        margin: 5px 0;
                        font-size: 1em;
                    }
                    p {
                        font-size: 0.7em;
                    }
                    img{
                        max-height: 80px;
                        max-width: 80px;
                    }
                }
                .channel {
                    h2 {
                        margin: 15px 0;
                        font-size: 1.2em;
                    }
                    img{
                        max-width: 80px;
                        max-height: 80px;
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 1250px) {
    .background {
        .modal {
            width: 40%;
        }
    }
}
